<template>
  <div class="course-practise-library">
    <head-top title="题库">
      <van-icon class="head-right" name="plus" @click="toAdd"></van-icon>
    </head-top>

    <van-pull-refresh v-model="isLoading" :head-height="80" @refresh="getLibraryList" class="pull_fresh">
      <van-cell-group v-if="libraryList && libraryList.length>0">
        <van-cell class="cell_main" v-for="(item, index) in libraryList" :key="index" is-link :title="item.name"
                  :label="item.createTime" @click="libraryClick(item)">
          <template slot="icon">
            <van-icon class="cell_icon" name="orders-o"></van-icon>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

    <van-popup v-model="libraryOperate" position="bottom">
      <van-cell value="预览" @click.stop.prevent="toPreView">
        <van-icon slot="right-icon" class-prefix="iconfont icon-chakan_yulan"></van-icon>
      </van-cell>
      <van-cell value="编辑" @click.stop.prevent="toEdit">
        <van-icon slot="right-icon" name="edit" class-prefix="iconfont icon-edu-biji"></van-icon>
      </van-cell>
      <van-cell value="删除" @click.stop.prevent="toDrop">
        <van-icon slot="right-icon" class-prefix="iconfont icon-shanchu"></van-icon>
      </van-cell>
    </van-popup>

  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "practiseLibrary",
  data () {
    return {
      libraryList: [],
      isLoading: false,
      librarySelected: null,
      libraryOperate: false
    };
  },
  mounted () {
    this.getLibraryList();
  },
  methods: {
    getLibraryList () {
      this.libraryList = [];
      this.isLoading = true;
      this.$mainHttp.classPractise.LibraryList({
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.libraryList.push({
              name: item["PLTitle"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              type: item["PLType"],
              id: item["PLID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    libraryClick (item) {
      this.librarySelected = item;
      this.libraryOperate = true;
    },
    toPreView () {
      this.$store.commit("SET_PRACTISE_LIBRARY", this.librarySelected);
      this.$router.push("/practise-library/view");
    },
    toAdd () {
      this.$store.commit("SET_PRACTISE_LIBRARY", null);
      this.$router.push("/practise-library/title-edit");
    },
    toEdit () {
      this.$store.commit("SET_PRACTISE_LIBRARY", this.librarySelected);
      this.$router.push("/practise-library/edit");
    },
    toDrop () {
      Dialog.confirm({
        message: "确认删除当前题库？"
      }).then(() => {
        this.toDelete();
      }).catch(() => {
      });
    },
    toDelete () {
      this.$mainHttp.classPractise.LibraryDelete({
        ids: this.librarySelected.id,
        state: -1
      }).then(res => {
        if (res.Result) {
          this.libraryOperate = false;
          this.getLibraryList();
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.course-practise-library {
  min-height: 100%;
  background-color: #fff;

  .cell_icon{
    padding-right: 12px;
  }

  .pull_fresh {
    height: 100%;
  }
}
</style>
